import { Utility } from './../../../services/utility';
import { Session } from './../../../services/session';
import { Events } from 'ionic-angular';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { WorkflowProvider } from '../../../providers/workflow-provider';
import { ResponseObject } from '../../../models/models';
import { workflowCommon } from '../workflow-tabs/workflow.common';

/**
 * Generated class for the WorkflowTaskPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-workflow-task',
  templateUrl: 'workflow-task.html',
})
export class WorkflowTaskPage extends workflowCommon {

  startDate: any
  endDate: any

  page: number = 0; //当前页码
  count: number = 10; //单页显示数量 
  finiteScroll: Boolean; //是否还有更多数据   
  buttons: any[];
  tasklist: any[];
  showlist: any[];
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public events: Events,
    public workflowProvider: WorkflowProvider,
    public session: Session) {
    super(navCtrl, events);
    this.startDate = Utility.dateFormatByMiles(new Date().getTime() - 7 * 24 * 3600 * 1000)
    this.endDate = Utility.dateFormat(new Date());

    this.getMyTasks();
  }

  /**
   * 根據日期區間獲取我的單
   * @param _event 
   */
  getMyTasks(_event?) {
    this.workflowProvider.getMyProcess(this.startDate, this.endDate).subscribe((data: ResponseObject) => {
      if (data.errcode == 0) {
        this.tasklist = data.result;
      }
      super.completeRefresh(_event)
    }, err => {
      super.completeRefresh(_event)
    });
  }

  /**调用父类返回方法 */
  goBack() {
    super.goBack();
  }

  /**调用父类跳到任务详情方法 */
  goDetails(item) {
    item['app_isEdit'] = false;
    item['isFinished'] = true;
    super.goDetails(item)
  }

  /**刷新 */
  doRefresh(_event) {
    this.getMyTasks(_event);
  }
  //加载更多数据
  doInfinite(infiniteScroll) {
    //this.createArray();
    setTimeout(() => {
      infiniteScroll.complete();
    }, 1000);
  }
 


}



